<template lang="html">
  <div class="">
    <div class="">
      <content-loader
        v-if="!handleLoader"
    		:height="736"
    		:width="1024"
    		:speed="2"
        primaryColor="#f3f3f3"
  		  secondaryColor="#dcdcdc"
    	>
    		<rect x="44.6" y="52.83" rx="0" ry="0" width="933.24" height="60.06" />
    		<rect x="43.9" y="153.83" rx="0" ry="0" width="221.6" height="278.4" />
    		<rect x="753.9" y="153.83" rx="0" ry="0" width="221.6" height="278.4" />
    		<rect x="516.9" y="154.83" rx="0" ry="0" width="221.6" height="278.4" />
    		<rect x="280.9" y="154.83" rx="0" ry="0" width="221.6" height="278.4" />
    		<rect x="43.9" y="450.83" rx="0" ry="0" width="221.6" height="278.4" />
    		<rect x="279.9" y="450.83" rx="0" ry="0" width="221.6" height="278.4" />
    		<rect x="516.9" y="450.83" rx="0" ry="0" width="221.6" height="278.4" />
    	</content-loader>
    </div>
    <div class="study__report__wrapper" v-if="handleLoader">
      <h2 class="hidden-sm-and-up">Study Dashboard</h2>
      <h2 class="hidden-sm-and-down" style="color:#49c5fe;padding:0;margin:0;font-weight:400;" v-if="handleStdType">Trial Student</h2>
      <UserStatus :getB2CSummary="getB2CSummary" />
      <div class="study__bottom">
        <CertCompletion />
        <PointsGoal :getWeeklyProgress="getWeeklyProgress" />
        <ProgressGoal />
        <ComprehensionGoal :getWeeklyProgress="getWeeklyProgress" />
        <PronunciationGoal :getWeeklyProgress="getWeeklyProgress" />
        <ListeningGoal :getWeeklyProgress="getWeeklyProgress" />
        <SpeakingGoal :getWeeklyProgress="getWeeklyProgress" />
      </div>
    </div>
  </div>
</template>

<script>
import { ContentLoader } from 'vue-content-loader';
import UserStatus from '../../student-components/study-progress-report/UserStatus'
import CertCompletion from '../../student-components/study-progress-report/cert-completion/CertCompletionWrapper'
import ProgressGoal from '../../student-components/study-progress-report/progress-goal/ProgressGoalWrapper'
import PointsGoal from '../../student-components/study-progress-report/bar-chart-point/PointsGoal'
import ComprehensionGoal from '../../student-components/study-progress-report/bar-chart-point/ComprehensionGoal'
import PronunciationGoal from '../../student-components/study-progress-report/bar-chart-point/PronunciationGoal'
import ListeningGoal from '../../student-components/study-progress-report/bar-chart-point/ListeningGoal'
import SpeakingGoal from '../../student-components/study-progress-report/bar-chart-point/SpeakingGoal'

export default {
  components: {
    ContentLoader,
    UserStatus,
    CertCompletion,
    ProgressGoal,
    PointsGoal,
    ComprehensionGoal,
    PronunciationGoal,
    ListeningGoal,
    SpeakingGoal
  },
  created () {
    this.$store.dispatch('GET_STUDY_DASHBOARD')
    this.$store.dispatch('GET_STUDY_DASHBOARD_COACHING')
    this.$store.dispatch('GET_WEEKLY_PROGRESS')
    this.$store.dispatch('POST_B2C_SUMMARY')
  },
  computed: {
    getWeeklyProgress () {
      const data = this.$store.state.stateWeeklyProgress
      if (data == null) {
        return 0
      } else {
        return data
      }
    },
    getB2CSummary () {
      const data = this.$store.state.stateB2CSummary.data.data.data
      if (data == null) {
        return 0
      } else {
        return data
      }
    },
    handleLoader () {
      const data = this.$store.state.stateStudyDashboard
      const data2 = this.$store.state.stateWeeklyProgress
      const data3 = this.$store.state.stateB2CSummary
      const data4 = this.$store.state.stateStuDashCoaching

      if (data == null || data2 == null || data3 == null || data4 == null) {
        return false
      } else {
        return true
        // setTimeout(foo, 1000);
      }
    },
    handleStdType () {
      const data = this.$store.state.DataAdditional.type

      if (data == "regular") {
        return false
      } else {
        return true
      }
    }
  }
}
</script>

<style lang="scss">
  @import './StudyProgressReport.scss';
</style>



