<template lang="html">
  <div class="userstatus__wrapper">
    <div class="user__status" v-if="ResizeScreen == 3 || ResizeScreen == 2">
      <table v-if="!handleStatus">
        <tr>
          <th>User Status</th>
          <th>Exit Test</th>
          <th>Student Since</th>
          <th>Total Study Hours</th>
          <th>Last Studied Lesson</th>
          <th>Last Studied</th>
        </tr>
        <tr>
          <td>{{getB2CSummary.pt_result}}</td>
          <td>{{getB2CSummary.cert_level.exit_test_status}}</td>
          <td>{{getB2CSummary.register_date}}</td>
          <td>{{getB2CSummary.cert_level.total_study_hours}}</td>
          <td>{{getB2CSummary.cert_level.last_lesson_studied}}</td>
          <td>{{getB2CSummary.cert_level.last_study_session}}</td>
        </tr>
      </table>
      <div class="user__status--false" v-if="handleStatus">
        You haven't studied yet
      </div>
    </div>
    <div class="user__status--mobile" v-if="ResizeScreen == 1">
      <div class="user__status">
        <div class="user__status--false" v-if="handleStatus">
          You haven't studied yet
        </div>
      </div>
      <div class="" v-if="!handleStatus">
        <div class="status__card">
          <div class="status__card--top">
            User Status
          </div>
          <div class="status__card--bottom">
            {{getB2CSummary.pt_result}}
          </div>
        </div>
        <div class="status__card">
          <div class="status__card--top">
            Exit Test
          </div>
          <div class="status__card--bottom">
            {{getB2CSummary.cert_level.exit_test_status}}
          </div>
        </div>
        <div class="status__card">
          <div class="status__card--top">
            Student Since
          </div>
          <div class="status__card--bottom">
            {{getB2CSummary.register_date}}
          </div>
        </div>
        <div class="status__card">
          <div class="status__card--top">
            Total Study Hours
          </div>
          <div class="status__card--bottom">
            {{getB2CSummary.cert_level.total_study_hours}}
          </div>
        </div>
        <div class="status__card">
          <div class="status__card--top">
            Last Studied Lesson
          </div>
          <div class="status__card--bottom">
            {{getB2CSummary.cert_level.last_lesson_studied}}
          </div>
        </div>
        <div class="status__card">
          <div class="status__card--top">
            Last Studied
          </div>
          <div class="status__card--bottom">
            {{getB2CSummary.cert_level.last_study_session}}
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: [
    'getB2CSummary'
  ],
  data () {
    return {
      windowWidth: 0,
      windowHeigt: 0,
      ResizeScreen: null
    }
  },
  mounted() {
    this.$nextTick( () => {
      window.addEventListener('resize', this.getWindowWidth);
      window.addEventListener('resize', this.getWindowHeight);

      this.getWindowWidth()
      this.getWindowHeight()
    })
  },
  computed: {
    getStudyDashboard () {
      const data = this.$store.state.stateStudyDashboard
      if (data == null) {
        return 0
      } else {
        return data.data.data
      }
    },
    handleStatus () {
      const data = this.getB2CSummary.cert_level

      if (data == []) {
        return false
      } else {
        return true
      }
    }
  },
  methods: {
    getWindowWidth (event){
      this.windowWidth = document.documentElement.clientWidth;

      if (this.windowWidth <= 599){
        //this for resolution phone only
        this.ResizeScreen = 1
      } else if (this.windowWidth <= 768){
        //this for resolution Ipad Potrait
        this.ResizeScreen = 2
      } else if (this.windowWidth >= 1024){
        //this for resolution Dekstop-up
        this.ResizeScreen = 3
      }
    },
    getWindowHeight (event){
      this.windowHeigt = document.documentElement.clientHeight;
    }
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.getWindowWidth);
    window.removeEventListener('resize', this.getWindowHeight);
  }
}
</script>

<style lang="css">
</style>



